export const shakeArray = arr => {
  return arr.sort(() => Math.random() - 0.5);
};

export const updateState = (oldState, updates) => {
  return {
    ...oldState,
    ...updates
  };
};

export const processJSONfromGoogleSpreadsheet = data => {
  data = data.feed.entry;
  let arr = [];
  data.forEach((elem, index) => {
    arr.push({});
    arr[index].word = elem.gsx$word.$t.toUpperCase();
    arr[index].translation = elem.gsx$translation.$t.toUpperCase();
    arr[index].usage = elem.gsx$usage.$t.toLowerCase();
    arr[index].img = elem.gsx$img.$t;
    arr[index].shakenArray = shakeArray(
      elem.gsx$word.$t.toUpperCase().split("")
    );
  });
  return arr;
};

export const isMobile = () => {
  let devicesList = navigator.userAgent||navigator.vendor||window.opera;
  let expr = /(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i;
  return expr.test(devicesList);
};


